:root {
    --dunbar: 'DunbarText';
    --ave: 'Avenir';
    --avenext: 'Avenir Next';

    --primary: #D4AB41;
    --white: #ffffff;
    --black: #000000;
    --light: #A0A0A0;
}

/* Fonts */
@font-face {
    font-family: 'Avenir';
    src: url('../Fonts/Avenir/Avenir-LightOblique.eot');
    src: url('../Fonts/Avenir/Avenir-LightOblique.eot?#iefix') format('embedded-opentype'),
        url('../Fonts/Avenir/Avenir-LightOblique.woff') format('woff'),
        url('../Fonts/Avenir/Avenir-LightOblique.ttf') format('truetype'),
        url('../Fonts/Avenir/Avenir-LightOblique.svg#Avenir-LightOblique') format('svg');
    font-weight: 300;
    font-style: italic;
    font-display: swap;
}

@font-face {
    font-family: 'Avenir';
    src: url('../Fonts/Avenir/Avenir-Black.eot');
    src: url('../Fonts/Avenir/Avenir-Black.eot?#iefix') format('embedded-opentype'),
        url('../Fonts/Avenir/Avenir-Black.woff') format('woff'),
        url('../Fonts/Avenir/Avenir-Black.ttf') format('truetype'),
        url('../Fonts/Avenir/Avenir-Black.svg#Avenir-Black') format('svg');
    font-weight: 900;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Avenir';
    src: url('../Fonts/Avenir/Avenir-Medium.eot');
    src: url('../Fonts/Avenir/Avenir-Medium.eot?#iefix') format('embedded-opentype'),
        url('../Fonts/Avenir/Avenir-Medium.woff') format('woff'),
        url('../Fonts/Avenir/Avenir-Medium.ttf') format('truetype'),
        url('../Fonts/Avenir/Avenir-Medium.svg#Avenir-Medium') format('svg');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Avenir Next';
    src: url('../Fonts/Avenir/AvenirNext-Regular.eot');
    src: url('../Fonts/Avenir/AvenirNext-Regular.eot?#iefix') format('embedded-opentype'),
        url('../Fonts/Avenir/AvenirNext-Regular.woff') format('woff'),
        url('../Fonts/Avenir/AvenirNext-Regular.ttf') format('truetype'),
        url('../Fonts/Avenir/AvenirNext-Regular.svg#AvenirNext-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Avenir Next';
    src: url('../Fonts/Avenir/AvenirNext-Medium.eot');
    src: url('../Fonts/Avenir/AvenirNext-Medium.eot?#iefix') format('embedded-opentype'),
        url('../Fonts/Avenir/AvenirNext-Medium.woff') format('woff'),
        url('../Fonts/Avenir/AvenirNext-Medium.ttf') format('truetype'),
        url('../Fonts/Avenir/AvenirNext-Medium.svg#AvenirNext-Medium') format('svg');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'DunbarText';
    src: url('../Fonts/Dunbar/DunbarText-Bold.eot');
    src: url('../Fonts/Dunbar/DunbarText-Bold.eot?#iefix') format('embedded-opentype'),
        url('../Fonts/Dunbar/DunbarText-Bold.woff') format('woff'),
        url('../Fonts/Dunbar/DunbarText-Bold.ttf') format('truetype'),
        url('../Fonts/Dunbar/DunbarText-Bold.svg#DunbarText-Bold') format('svg');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

.form-group {
    margin: 0 0 15px;    
}
.form-group label {
    display: inline-block;
    margin-bottom: 3px;
    font-size: 13px;
    font-family: var(--ave);
    font-weight: 500;
}
.form-control,
input[type="text"],
input[type="email"],
input[type="password"],
textarea {
    width: 100%;            
    outline: none;
    border-color: transparent;
    padding: 10px 15px;
    border-radius: 5px;    
    font-family: var(--ave);
    font-size: 14px;
    color: var(--black);
}
.form-control:hover,
.form-control:focus,
input[type="text"]:hover,
input[type="email"]:hover,
input[type="password"]:hover,
textarea:hover {
    box-shadow: none !important;
    border-color: transparent;
}
.input[type="submit"],
.input[type="button"],
.btn {
    padding: 10px 15px;
    background-color: var(--primary);
    color: #000;
    font-size: 14px;
    font-family: var(--dunbar);    
    border-radius: 5px;
    border-color: var(--primary);
}

.input[type="submit"]:focus,
.input[type="button"]:focus,
.input[type="submit"]:hover,
.input[type="button"]:hover,
.btn:active,
.btn:focus,
.btn:hover {
    background-color: transparent;
    color: var(--white);    
    border-color: var(--primary);
    box-shadow: none !important;
}

.form-control.error {
    border-color:red;
}

button.close {
    padding: 0;
    background-color: transparent;
    border: 0;
}
.alert-dismissible .close {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    padding: .75rem 1.25rem;
    color: inherit;
}
.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0,0,0,0);
    white-space: nowrap;
    border: 0;
}
.as-link {
    cursor: pointer;
}

body {
    font-family: var(--ave);
    font-size: 16px;
    line-height: 1.2em;
    color: var(--black);
}

.container {
    max-width: 1200px;
    margin: 0 auto;    
}



.loader {
    width: 100%;
    height:100%;
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    align-items: center;
    justify-content: center;
    background-color: #000;
}
.loader-logo {
    position: relative;
}
.loader-logo img {
    max-width: 130px;
}
.loader-logo:after {    
    width: 35px;
    height: 35px;
    content: "";
    background-image: url("../images/loader.gif");
    background-size: 100% 100%;
    position: absolute;
    top: 100%;
    left: 50%;

    -webkit-transform: translate(-50%,0);
    -moz-transform: translate(-50%,0);
    transform: translate(-50%,0);
} 

.app-container {
    position: relative;
}
.app-container:before {
    content: "";
    width: 100%;
    height: 100vh;
    display: inline-block;
    position: fixed;
    top: 0;
    left: 0;
    background-image: url("../images/bg-img.png");
    background-size: cover;
    z-index: -2;
}
.app-container:after {
    content: "";
    width: 100%;
    height: 100vh;
    display: inline-block;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.94;
    z-index: -1;    
}

.site-header {
    padding: 20px 0;
}
.site-header .logo img {
    max-width: 110px;
    height: auto;
}
.site-header .row {
    align-items: center;
}
.navigation-wrapper {
    display: flex;
    flex-direction: column-reverse;
    text-align: right;
}
.navigation {
    
}
.navigation ul {
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.navigation ul li { 
    list-style: none;   
    font-size: 14px;
    color: var(--white);
    border-bottom: 1px solid var(--white);
    margin-left: 15px;
}
.navigation ul li span{
    cursor: pointer;
    font-family: var(--avenext);
    font-weight: normal;
}
.navigation ul li:last-child {
    border: none;
}
.navigation ul li span.btn{
    font-family: var(--dunbar);
    font-weight: bold;
}

.donation-text {
    font-family: var(--avenext);
    font-size: 14px;
    color: var(--white);
    font-weight: 500;
}
.donate-btn {
    font-family: var(--dunbar);
    font-size: 16px;
    font-weight: bold;
    color: var(--primary);
    border-bottom: 0.5px solid rgba(255,255,255,0.35);
    padding-bottom: 2px;
    cursor: pointer;
}

/* Footer */
.footer {

}
.footer .row {
    align-items: flex-end;
}
.count-downtext {    
    display: flex;    
    align-items: center;
}
.count-downtext > span {
    color: var(--primary);
    font-family: var(--dunbar);
    font-weight: bold;
    font-size: 14px;
    line-height: 1;
}
.timer-wrapper span {
    margin-left: 5px;
    font-weight: 500;
    font-size: 12px;
    color: var(--white);
    line-height: 1;
}
.store-linktext p {
    font-size: 14px;
    color: var(--white);
    line-height: 1.3em;
    margin: 0;
    text-align: right;
}
.store-linktext p a {
    font-family: var(--dunbar);
    color: var(--primary);
    font-size: 20px;
    text-decoration: none;
    margin-top: 10px;
    display: inline-block;
    padding-bottom: 2px;
    position: relative;    
}
.store-linktext p a:after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: rgba(255,255,255,0.35);
    position: absolute;
    bottom: -5px;
    left: 0;
}

.page-wrapper{
    margin-top: 25px;
}

.left-side-text {
    color: var(--white);
    padding-right: 25px;
}
.left-side-text h2{
    font-family: var(--dunbar);
    font-size: 50px;
    line-height: 1.3em;
    margin: 0 0 0px;
}
.left-side-text h3{
    font-family: var(--dunbar);
    color: var(--primary);
    font-size: 27px;
    line-height: 1em;
    margin: 0 0 5px;
}
.left-side-text h4{
    font-family: var(--dunbar);
    color: var(--primary);
    font-size: 20px;
    line-height: 1em;
    margin: 10px 0 30px;
}
.left-side-text p{
    font-family: var(--ave);
    font-weight: 500;
    font-size: 16px;
    line-height: 1.3em;
}
.left-side-text p span{
    font-family: var(--ave);
    font-weight: 900;
}

.left-side-list {
    margin: 35px 0 15px;
}
.left-side-list h4{
    color: var(--primary);
    font-size: 20px;
    font-family: var(--dunbar);
    margin: 0 0 15px;
}
.left-side-list ul {
    margin: 0;
    padding: 0;
}
.left-side-list ul li {
    font-family: var(--ave);
    font-weight: 500;
    line-height: 1.2em;
    font-size: 16px;
    margin-bottom: 15px;
    padding-left: 25px;
    position: relative;
    list-style: none;
}
.left-side-list ul li:before{
    content: "";
    width: 15px;
    height: 18px;
    background-image: url("../images/fiest-1.png");
    background-size: 100% auto;
    background-repeat: no-repeat;
    position: absolute;
    top: 0;
    left: 0;
}
.left-side-list ul li.two:before {
    background-image: url("../images/fiest-2.png");
}
.left-side-list ul li.three:before {
    background-image: url("../images/fiest-3.png");
}

.want-in-form-wrapper {
    margin: 40px 0 15px;
}
.want-in-form-wrapper span {
    font-size: 12px;
    margin: 0 0 15px;
    display: inline-block;
}
.want-in-form {
    display: flex;
    align-items: center;
}
.want-in-form input[type="email"]:focus,
.want-in-form input[type="email"]{
    flex: 1;
    padding:10px 15px;
    border-radius: 5px;
    margin-right: 10px;
    outline: none;    
}
.want-in-form input.btn{}


.video-play-wrapper {
    color: var(--white);
    text-align: center;
}
.video-play-wrapper h2 {
    font-size: 15px;
    font-family: var(--ave);
    font-weight: 300;
    font-style: italic;
    color: var(--white);
}
.video-play-wrapper h3 {
    font-size: 13px;
    font-family: var(--ave);
    font-weight: 300;
    color: var(--light);
}
.video-play-wrapper .play-btn img{
    margin: 15px 0;
    max-width: 99px;
    cursor: pointer;
}
.video-play-wrapper .learn-more-text {
    font-family: var(--dunbar);
    color: var(--primary);
    padding-bottom: 5px;
    border-bottom: 1px solid var(--primary);
    display: inline-block;
}

.mobile-wrapper {
    color: var(--white);
    text-align: right;
    position: relative;
}
.mobile-wrapper > img {
    max-width: 100%;
}
.mobile-wrapper-text {
    margin: 30px 0 0;
}
.mobile-wrapper-text p{
    text-align: right;
    color: var(--white);
    font-size: 12px;
    font-family: var(--avenext);
    font-weight: 500;
}
.video-wrapper {
    position: absolute;
    top: 0;
    right: 37%;
    height: 100%;
}
.video-wrapper > img {
    height: 479px;
    max-height: 100%;
    position: relative;
    z-index: 1;    
}
.video-wrapper video {
    position: absolute;
    width: 100%;
    height: 94%;
    top: 2.4%;
    left: 0;
    z-index: 0;
}

/* Modal Box */
.modal {

}
.modal-content {
    background: #efefef;
    border-radius: 10px;
    border: none;
}
.modal-header {
    border: none;
}
.modal-header .close {
    width: 30px;
    height: 30px;
    background: var(--primary);
    border-radius: 50%;
    text-align: center;
    font-size: 21px;
    font-family: var(--dunbar);
    padding: 0;
    line-height: 1;
    position: absolute;
    right: -15px;
    top: -15px;
    border: 1px solid #000;
    cursor: pointer;
}
.modal-content-wrapper {
    margin-bottom: 25px;
    margin-top: -15px;
}
.modal-content-wrapper h2 {
    font-family: var(--dunbar);
    font-size: 20px;
    margin: 0 0 15px;
}
.modal-content-wrapper p {
    font-family: var(--ave);
    font-weight: 500;
    font-size: 14px;
    margin: 0 0 5px;
    line-height: 1.4em;
}
.modal-footer {
    padding: 0 .75rem .75rem;
    border:none
}
.btn-wrapper {
    margin: 10px 0 0;
}
.btn-wrapper .btn:active,
.btn-wrapper .btn:focus,
.btn-wrapper .btn:hover {
    color: var(--black);
}

.videoModal {
    position: fixed;
    width: 100%;
    height:100%;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 5;
}
.videoModal:after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    background: #000;
    opacity: 0.85;
}
.videoModal .video {
    z-index: 3;
    position: relative;
    background-color: #efefef;
    border-radius: 10px;
    opacity: 0;

    -webkit-transform: translateY(-10%);
    -moz-transform: translateY(-10%);
    transform: translateY(-10%);

    -webkit-transition: all 0.5s ease 0s;
    -moz-transition: all 0.5s ease 0s;
    transition: all 0.5s ease 0s;
}
.videoModal .video.show {
    opacity: 1;

    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    transform: translateY(0);

}
.videoModal .video .close {
    width: 30px;
    height: 30px;
    background: var(--primary);
    border-radius: 50%;
    text-align: center;
    font-size: 21px;
    font-family: var(--dunbar);
    padding: 0;
    line-height: 1;
    position: absolute;
    right: -15px;
    top: -15px;
    border: 1px solid #000;
    cursor: pointer;
}
.StripeCheckout {    
    background: transparent !important;  
    box-shadow: none !important;
}
.StripeCheckout span {
    padding: 10px 15px !important;
    background: var(--primary) !important;
    color: #000 !important;
    font-size: 14px !important;
    font-family: var(--dunbar) !important;    
    border-radius: 5px !important;
    border-color: var(--primary) !important; 
    height: auto !important;
    text-shadow: none !important;
    line-height: 1.2em !important;
}